import React,{useEffect,useState} from 'react';
import Recipe from './Recipe';
import './App.css';

const App =() => {

  const APP_ID = "975ee6c5";
  const APP_KEY= "99962e80839965a2e68d765ffd244990";

  const[recipes, setRecipes] = useState([]);
  const[search, setSearch] = useState("");
  const[query, setQuery] = useState('chicken');


  
  useEffect(() =>{
    getRecipes();
    
  }, [query] );
  
    const  getRecipes = async () =>{
    const response = await fetch (
      `https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`
      );

        const data = await response.json();
        setRecipes(data.hits);
  
    
  };

  const updateSearch = e => {
    setSearch(e.target.value);
    
  };

  const getSearch = e =>{
    e.preventDefault();
    setQuery(search);
    setSearch('');
  }


  return(
    <div className="App">
      <p><center> <h1>Welcome!! Foodizzz</h1><br/> Here you Just need to search for any items and you can jst see the magic it does my giving some related Dishes for you <b>Keyword</b> <br/>
      <i><h3> Remenber : Its just made to show use of api which is ofcourse free so it may not be up to the mark</h3></i>
      </center></p>
      <form  onSubmit={getSearch} className="search-form">
        <input className="search-bar" type ="text" value={search} onChange={updateSearch} />
        <button  
          className = "search-button" 
          type ="submit">
          Search
        </button>
      </form>  
      <div className="recipes">
        {recipes.map(recipe => (
          <Recipe 
            key ={recipe.recipe.label}
            title={recipe.recipe.label} 
            calories={ recipe.recipe.calories}
            image = {recipe.recipe.image}
            ingredients = {recipe.recipe.ingredients}
        />
      ))}       
    </div>
    </div>
  );
};

export default App;
